  private getDataInvoice(bill) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill_details_byId_bill(bill._id)
      ]).then(([invoiceDetail]: any) => {
        console.log(invoiceDetail);
        if (!invoiceDetail.length)
          this.init(bill, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(bill, invoiceDetail)
        }
        this.vhComponent.hideLoading(0)
      })
    })
  }

  init(invoice: VhOrderInvoice = null, invoiceDetail: VhOrderInvoiceDetail[] = null) {
    console.log('init invoice');
    console.log(invoice);
    this.invoice = new VhOrderInvoices(invoice, invoiceDetail, this.vhQueryCafe)
    if (invoice && invoiceDetail) {
      // set nhn vin mc nh
      if (!this.invoice.getIdEmployee())
        this.invoice.setIdEmployee(this.vhAuth.getUser()._id)
      this.invoice.setDate(new Date().toISOString())
      // this.invoice.setPayment(invoice.total)
      this.note = this.invoice.getNote();
      this.invoice.setIdTable("go_home")
      this.invoice.setIdArea("go_home");
      this.pickerTime = new Date(this.invoice.getDate());
      this.observableCheckBill(this.invoice.getID());
      this.percent_discount_bill = this.invoice.getSubTotal() ? (this.invoice.getDiscount() / this.invoice.getSubTotal()) * 100 : 0
      this.employee = this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee());
      
      
    }
  }
  // s kin subscribe bill dc m  thit b khc
  observableCheckBill(id_bill) {
    this.vhQueryCafe.observableSyncOpeningBill(id_bill, 36).subscribe((rsp: any) => {
      this.vhQueryCafe.closeSyncOpeningBill();
      if (rsp.vcode === 0) {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate("Ha n ny ang c cp nht  thit b khc!"),
          nzOkText: "Thot",
          nzOnOk: () => {
            this.goBack();
          },
          nzCancelText: null,
          nzOnCancel: () => {
            this.goBack();
          }
        })
      } else if (rsp.vcode === 1) {
        this.nzModalService.confirm({
          nzTitle: this.languageService.translate("Ha n ny  c thanh ton  thit b khc!"),
          nzOkText: "Thot",
          nzOnOk: () => {
            this.goBack();
          },
          nzCancelText: null,
          nzOnCancel: () => {
            this.goBack();
          }
        })
      } else if (rsp.vcode === 2) {
        this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Ha n  b xa!"))
        this.goBack();
      }
    }, error => {
      this.vhComponent.hideLoading(0).then(() => {
        this.vhComponent.showToast(5000, error, "alert-toast")
      })
    })
  }
  event: any // bin lng nghe
  ngOnDestroy() {
    document.removeEventListener('keydown', this.event, false);
    if (this.invoice.getID()) this.vhQueryCafe.closeSyncOpeningBill();
  }
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 112 || keycode === 113 || keycode === 114) event.preventDefault();
      if (keycode === 115) this.searchProductList = !this.searchProductList;
      else if (this.invoice.getInvoiceDetail().length > 0) {
        switch (keycode) {
          case 112:
            this.payInvoice(1, false);
            break;
          case 113: {
            if (this.checkPrinter) this.payInvoice(1, true)
            break;
          }
        }
      }
    }

    // //s kin f5, close tab...
    // this.closerBrower = (event: BeforeUnloadEvent) => {
    //   (event || window.event).returnValue = 'Changes you made may not be saved';
    //   return 'Changes you made may not be saved';
    // }
    // window.addEventListener('beforeunload', this.closerBrower)
    document.addEventListener('keydown', this.event, false);
  }
