import { NzModalService } from 'ng-zorro-antd/modal';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe, VhQuery } from 'ionic-vhframeworks';
import { SalesA4Component } from 'src/app/cafe/components/print/sales/sales-a4/sales-a4.component';
import { SalesA5Component } from 'src/app/cafe/components/print/sales/sales-a5/sales-a5.component';
import { SalesK80Col4Component } from 'src/app/cafe/components/print/sales/sales-k80-col4/sales-k80-col4.component';
import { SalesK80Col3Component } from 'src/app/cafe/components/print/sales/sales-k80-col3/sales-k80-col3.component';
import { SalesK57Col3Component } from 'src/app/cafe/components/print/sales/sales-k57-col3/sales-k57-col3.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component'; 
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhOrderInvoice, VhOrderInvoiceDetail, VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';

@Component({
  selector: 'app-invoice-detail',
  templateUrl: './detail-invoice.component.html',
  styleUrls: ['./detail-invoice.component.scss']
})
export class DetailInvoiceComponent {
  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhOrderInvoices
  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  invoiceToday : any = this.router.getCurrentNavigation().extras.state.invoiceToday
  constructor(
    private router: Router,
    public vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private modal: NzModalService,
    private fncService: FunctionService
  ) {
    let bill = this.router.getCurrentNavigation().extras.state.bill
    this.getDataInvoice(bill)
    this.init()
    console.log(bill);
    
    this.invoiceInfo = { employee: {}, customer: bill.customer, paymentName: '' }
  }

  init(invoice: VhOrderInvoice = null, invoiceDetail: VhOrderInvoiceDetail[] = null) {
    this.invoice = new VhOrderInvoices(invoice, invoiceDetail, this.vhQueryCafe)
    
  }

  private getDataInvoice(bill) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill_details_byId_bill(bill._id)
      ]).then(([ invoiceDetail]: any) => {
        console.log(invoiceDetail);
        if (!invoiceDetail.length)
          this.init(bill, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(bill, invoiceDetail)
        }
        this.vhComponent.hideLoading(0)
      })
    })
  }

  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units,ratio).unit
  }

  getLotNumber(lots, _id){
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' +  this.fncService.formatDate(lot.date_exp)
  }
  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("sell_enable_cancel_bill")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the sales invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice.getID()).then(() => {
              if (this.vhQueryCafe.getlocalCustomer(this.invoice.getIdPartner()).earning_point && this.invoice.getBillType() == 1) this.vhQueryCafe.updateCustomer(this.invoice.getIdPartner(), { earning_point: this.vhQueryCafe.getlocalCustomer(this.invoice.getIdPartner()).earning_point - this.invoice.getEarningPointBill() })
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000,
                  `${this.languageService.translate("Sales invoice")} ${this.invoice.getBillCode()} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                this.router.navigate(['/cafe/sales-desktop'], { state: { isSync: true } })
              })
            })
          })
        }, () => { })
    } else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQueryCafe.getlocalWallet(this.invoice.getIdWallet()).name
    }
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  public goToCart() {
    this.router.navigate(['/cafe/bill-web-app/sales-order/add'], { 
      state: { 
        invoiceDetail: this.invoice.getInvoiceDetail(), 
        invoice: this.invoice.getCreateUpdateInvoice(),
        customer : this.invoiceInfo.customer
       } 
      })
  }
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales')
      if (printer) resolve(printer)
      else rejects(false)
    })
  }

  printInvoice() {
    this.checkPrint().then((printer: any) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer.print_size_default), { printer: { ...printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)], name: "Sales" }, invoice: this.invoice }, false, false, `modal-print-${printer.print_size_default}`)
    }, (err) => {
      this.modal.confirm({
        nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
        nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
        nzIconType: "info-circle",
        nzCancelText: null,
        nzOnOk: () => console.log('OK'),
      });
    })
  }

  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesK57Col3Component
      case 'print_size_k80_3c': return SalesK80Col3Component
      case 'print_size_k80_4c': return SalesK80Col4Component
      case 'print_size_a5': return SalesA5Component
      case 'print_size_a4': return SalesA4Component
      default: return SalesA4Component
    }
  }
  goBack() {
    this.router.navigate(['/cafe/bill-web-app/sales-order'], { state: { isSync: true,invoiceToday : this.invoiceToday } });
  }
  ngAfterViewChecked() {
    if (document.getElementById('sales-invoice-detail-today') && document.querySelector(".sales-cart-header") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.getElementById('sales-invoice-detail-today').clientHeight - document.querySelector(".sales-cart-header").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
    }
    this.cdRef.detectChanges();
  }
}
