import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AddComponent } from './add/add.component';
import { DetailInvoiceComponent } from './detail-invoice/detail-invoice.component';
import { SalesOrderComponent } from './sales-order.component'; 
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core'; 
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { SearchModule } from 'src/app/cafe/components/search/search.module'; 
import { HeaderAccountInfoModule } from '../header-account-info/header-account-info.module';
import { ComponentsModule } from '../components/components.module';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';


const routes: Routes = [
  {
    path: '',
    component: SalesOrderComponent,
  }, 
  {
    path: 'add',
    component: AddComponent,
  },
  {
    path: 'detail',
    component: DetailInvoiceComponent,
  }, 
]

@NgModule({
  declarations: [
    SalesOrderComponent,AddComponent,DetailInvoiceComponent

  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule, 
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    IonicModule,
    SearchModule,
    RouterModule.forChild(routes),
    ComponentsModule,
    HeaderAccountInfoModule
  ]
})
export class SalesOrderModule { }
